.section-program {
  position: relative;
  height: 2050px;
  padding-top: 50px;
  background-color: $background-color-black;
  @media screen and (min-width: $breakpoint-tablet) {
    height: 1355px;
  }
  @media screen and (min-width: $breakpoint-desktop) {
    height: 1237px;
    padding-top: 120px;
    padding-bottom: 363px;
  }
}
.section-program::before {
  display: none;
  @media screen and (min-width: $breakpoint-desktop) {
    content: '';
    position: absolute;
    top: 294px;
    display: block;
    width: 100%;
    height: 1px;
    background-color: $background-color-white;
  }
}
.section-program .section-title {
  margin-bottom: 43px;
  color: $text-color-white;
  text-align: center;
  @media screen and (min-width: $breakpoint-tablet) {
    font-size: 26px;
    text-align: left;
    margin-bottom: 52px;
  }
  @media screen and (min-width: $breakpoint-desktop) {
    font-size: 46px;
    margin-bottom: 160px;
  }
}
.program-list {
  position: relative;
  list-style: none;
  display: flex;
  flex-direction: column;
  @media screen and (min-width: $breakpoint-tablet) and (max-width: $breakpoint-desktop - 1px) {
    transform: translateX(95px);
  }
  @media screen and (min-width: $breakpoint-desktop) {
    flex-direction: row;
    justify-content: space-between;
  }
}
.program-list::before {
  content: '';
  position: absolute;
  top: 10px;
  left: -20.5px;
  display: block;
  width: 1px;
  height: 1280px;
  background-color: $background-color-white;
  @media screen and (min-width: $breakpoint-tablet) {
    left: -50.5px;
    height: 717px;
  }
  @media screen and (min-width: $breakpoint-desktop) {
    display: none;
  }
}
.program-list-item {
  position: relative;
  @media screen and (min-width: $breakpoint-desktop) {
    width: calc(20% - 24px);
  }
}
.program-list-item::before {
  content: '';
  position: absolute;
  top: 8px;
  left: -26px;
  display: block;
  height: 12px;
  width: 12px;
  border-radius: 50%;
  background-color: $accent-color;
  @media screen and (min-width: $breakpoint-tablet) {
    left: -56px;
  }
  @media screen and (min-width: $breakpoint-desktop) {
    height: 16px;
    width: 16px;
    left: 0;
    top: -52px;
  }
}
.program-list-item:not(:last-child) {
  margin-bottom: 40px;
  @media screen and (min-width: $breakpoint-tablet) {
    margin-bottom: 30px;
  }
}
.semester {
  margin-bottom: 5px;
  font-weight: 700;
  font-size: 14px;
  line-height: 2;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: $text-color-white;
  @media screen and (min-width: $breakpoint-desktop) {
    font-size: 20px;
    &::first-letter {
      font-size: 30px;
    }
    margin-bottom: 25px;
  }
}
.semester-program {
  display: flex;
  flex-direction: column;
  @media screen and (min-width: $breakpoint-tablet) and (max-width: $breakpoint-desktop - 1px) {
    flex-direction: row;
  }
}
.semester-topic-list {
  list-style: none;
  margin-bottom: 8px;
  @media screen and (min-width: $breakpoint-tablet) and (max-width: $breakpoint-desktop - 1px) {
    width: 150px;
    margin-bottom: 0px;
    margin-right: 50px;
  }
}
.semester-topic p {
  margin-bottom: 5px;
  font-weight: 700;
  font-size: 12px;
  line-height: 2.649; //oririnally it's 1.649, but it does not fit the look in the template
  letter-spacing: 0.08em;
  color: $accent-color;
  @media screen and (min-width: $breakpoint-desktop) {
    font-size: 20px;
    margin-bottom: 10px;
  }
}
.semester-topic-item {
  color: $text-color-grey;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.67;
  letter-spacing: 0.08em;
  opacity: 0.5;
  @media screen and (min-width: $breakpoint-desktop) {
    font-size: 18px;
    line-height: 28px;
  }
}
.subsection {
  position: absolute;
  left: calc(50% - 140px);
  bottom: -232px;
  width: 280px;
  background-color: transparent;
  box-shadow: -5px 8px 26px rgba(0, 0, 0, 0.04);
  z-index: 10;
  @media screen and (min-width: $breakpoint-tablet) {
    left: calc(50% - 344px);
    bottom: -252px;
    width: 688px;
  }
  @media screen and (min-width: $breakpoint-desktop) {
    left: calc(50% - 657px);
    bottom: -200px;
    width: 1314px;
  }
}
.subsection .section-title {
  background-color: transparent;
  width: 60%;
  text-align: left;
  padding-bottom: 30px;
  @media screen and (min-width: $breakpoint-tablet) {
    width: 100%;
    font-size: 26px;
    text-align: center;
    letter-spacing: 0.03em;
  }
  @media screen and (min-width: $breakpoint-desktop) {
    display: none;
  }
}
.why-list {
  padding-top: 20px;
  padding-bottom: 20px;
  list-style: none;
  display: flex;
  align-items: center;
  flex-direction: column;
  flex-wrap: wrap;
  background-color: $background-color-white;
  @media screen and (min-width: $breakpoint-tablet) {
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    padding-top: 30px;
  }
  @media screen and (min-width: $breakpoint-desktop) {
    padding-top: 70px;
    padding-bottom: 70px;
  }
}
.why-item {
  width: 75%;
  @media screen and (min-width: $breakpoint-tablet) {
    width: 33%;
    margin-right: 20px;
  }
  @media screen and (min-width: $breakpoint-desktop) {
    width: 27%;
  }
}
.why-item:not(:last-child) {
  margin-bottom: 20px;
}
.why-item ul {
  list-style: none;
}
.why-number {
  color: $accent-color;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.649;
  margin-bottom: 10px;
  @media screen and (min-width: $breakpoint-desktop) {
    font-size: 20px;
  }
}
.why-reason {
  color: $text-color-black;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.428;
  letter-spacing: 0.08em;
  @media screen and (min-width: $breakpoint-desktop) {
    font-size: 20px;
  }
}
